
import {
  defineComponent,
  ref,
  reactive,
  onMounted,
  onUpdated,
  onUnmounted,
} from "vue";

export default defineComponent({
  name: "hello world",
  props: {
    msg: String,
  },
  setup(props) {
    // interface Data {
    //   count: number;
    //   object: object;
    // }
    const count = ref(0);
    const object = reactive({ foo: "bar" });
    const increase = (): void => {
      count.value++;
    };
    const list: any = [
      {
        name: "foo",
      },
      {
        name: "bar",
      },
      {
        name: "coo",
      },
    ];
    function test(x: number): string {
      console.log(x);
      return props.toString();
    }
    test(1);
    // test('number');
    // 生命周期
    onMounted(() => {
      console.log("mounted vue3 typescript");
    });
    onUpdated(() => {
      console.log("updated vue3 typescript");
    });
    onUnmounted(() => {
      console.log("onUnmounted vue3 typescript");
    });
    // 暴露给模板
    return {
      count,
      increase,
      object,
      list,
    };
    // return () => h('div', [count.value, object.foo])
  },
});
