import { createRouter, createWebHashHistory } from "vue-router";
import V2Home from "../views/v2/Home.vue";
import V1Home from "../views/v1/Home.vue";

const routes: Array<any> = [
  {
    path: "/",
    name: "Home",
    component: V2Home,
  },
  {
    path: "/v2",
    name: "Home",
    component: V2Home,
  },
  {
    path: "/v1",
    name: "Home",
    component: V1Home,
  },
  {
    path: "/v1/vue",
    name: "Vue Info",
    component: () =>
      import(/* webpackChunkName: "vueinfo" */ "../views/v1/Vue.vue"),
  },
  {
    path: "/v1/react",
    name: "React Info",
    component: () =>
      import(/* webpackChunkName: "reactinfo" */ "../views/v1/React.vue"),
  },
  {
    path: "/v1/vue3-pixel-art",
    name: "Pixel Art",
    component: () =>
      import(/* webpackChunkName: "pixelartinfo" */ "../views/v1/PixelArt.vue"),
  },
  {
    path: "/v1/vue3-todomvc",
    name: "vue3 todomvc",
    props: (route: any) => ({ condition: route.query.q }),
    component: () =>
      import(/* webpackChunkName: "todosinfo" */ "../views/v1/Todomvc.vue"),
  },
  {
    path: "/v1/vue3-markdown",
    name: "vue3 markdown",
    component: () =>
      import(/* webpackChunkName: "narkdowninfo" */ "../views/v1/Markdown.vue"),
  },
  {
    path: "/v1/vue3-apexcharts",
    name: "vue3 apexcharts",
    component: () =>
      import(/* webpackChunkName: "ApexCharts" */ "../views/v1/ApexCharts.vue"),
  },
  {
    path: "/v1/vue3-win10-DatePanel",
    name: "vue3 win10 DatePanel",
    component: () =>
      import(
        /* webpackChunkName: "Win10DatePanel" */ "../views/v1/Win10DatePanel.vue"
      ),
  },
  // {
  //   path: "/v1/vue3-svg",
  //   name: "vue3 svg",
  //   component: () =>
  //     import(/* webpackChunkName: "narkdowninfo" */ "../views/v1/Svg.vue"),
  // },
  {
    path: "/v1/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/v1/About.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

export default router;
